@use 'src/styles/mixin';
@use 'src/styles/variables';
@import "src/styles/extend";

.list-container {
  min-width: 250px;
  height: fit-content;
  background-color: rgba(255, 255, 255, 0.3);
  @extend %container-style;
  @include mixin.container(10px, 5px, 10px);

  h2 {
    margin-top: 0;
  }

  .list-title {
    margin: 10px;
  }
}
